	module.exports = {"data":{"site":{"siteMetadata":{"title":"Solar Sailer","description":"I am a developer, designer and product manager from France. Co-founder of Pixelnest Studio.","author":"Matthieu Oger","siteUrl":"https://solarsailer.net/"}},"markdownRemark":{"html":"<p><a href=\"http://www.elezea.com/2013/07/hey-marketers/\">Hey Marketers, making a website is not about you</a> (emphasis mine):</p>\n<blockquote>\n<p>The problem with many Marketers today is that they too often make it all about the company, and not about user needs. I’m sorry, but it’s not about changing people, and it’s not about making them do stuff. That’s old school thinking from a time when brands could steamroll their way into the consciousness and wallets of people through clever advertising and sleight of hand. Those days are over. Now our job is not to make it about how awesome we are, <strong>but how well we help people accomplish their goals</strong>.</p>\n</blockquote>\n<p>Further readings related to this subject:</p>\n<ul>\n<li><a href=\"http://www.90percentofeverything.com/2013/07/23/the-slippery-slope/\">The slippery slope</a></li>\n<li><a href=\"http://seriouspony.com/blog/2013/7/24/your-app-makes-me-fat\">Your app makes me fat</a></li>\n</ul>","frontmatter":{"date":"2013-08-13","readableDate":"13 August 2013","title":"Marketers","showPageTitle":null,"excerptLine":null,"excerptQuote":{"name":"Rian Van Der Merwe","quote":"The problem with many Marketers today is that they too often make it all about the company, and not about user needs."}},"fields":{"path":"/2013/08/marketers/"}}},"pathContext":{"id":"/Users/ashen/Code/solarsailer.net/content/posts/2013-08-13-marketers.md absPath of file >>> MarkdownRemark"}}

